"use client"

import React, { useEffect, useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { LucideIcon, Home, Settings, Users, BarChart2, MessageSquare, AlertCircle, LogOut, LogIn, Utensils, ScanLine } from "lucide-react"
import { cn } from "../lib/utils"
import { OwnerLogin } from "./OwnerLogin"

interface NavItem {
  name: string
  url: string
  icon: LucideIcon
}

interface NavBarProps {
  items?: NavItem[]
  className?: string
}

const defaultItems: NavItem[] = [
  { name: "Dashboard", url: "/", icon: Home },
  { name: "Students", url: "/students", icon: Users },
  { name: "Food Menu", url: "/menu", icon: Utensils },
  { name: "Analytics", url: "/analytics", icon: BarChart2 },
  { name: "Scan QR", url: "/scan", icon: ScanLine },
  { name: "Settings", url: "/settings", icon: Settings },
  { name: "Contact Support", url: "/support", icon: MessageSquare },
  { name: "Raise Complaint", url: "/complaint", icon: AlertCircle },
]

export function OwnerNavBar({ items = defaultItems, className }: NavBarProps) {
  const [activeTab, setActiveTab] = useState(items[0]?.name || "")
  const [isMobile, setIsMobile] = useState(false)
  const [isLoggedIn, setIsLoggedIn] = useState(true) // Set to true for temporary data
  const [showLoginModal, setShowLoginModal] = useState(false)
  const router = useRouter()

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768)
    }

    handleResize()
    window.addEventListener("resize", handleResize)
    return () => window.removeEventListener("resize", handleResize)
  }, [])

  const handleLogout = () => {
    setIsLoggedIn(false)
    router.push('/')
  }

  const handleLoginSuccess = () => {
    setIsLoggedIn(true)
    setShowLoginModal(false)
  }

  return (
    <>
      <div
        className={cn(
          "fixed top-6 left-1/2 -translate-x-1/2 z-50",
          className,
        )}
      >
        <div className="flex items-center gap-1 bg-[#1a1b1e]/95 border border-gray-800 backdrop-blur-xl py-2 px-3 rounded-2xl shadow-lg">
          {items.map((item) => (
            <Link
              key={item.name}
              href={item.url}
              className={cn(
                "flex items-center gap-2 px-4 py-2.5 text-sm font-medium text-gray-400 hover:text-white hover:bg-white/5 rounded-xl transition-all duration-200 ease-in-out",
                activeTab === item.name && "bg-white/10 text-white font-semibold"
              )}
              onClick={() => setActiveTab(item.name)}
            >
              <item.icon size={18} strokeWidth={2} className="transition-transform duration-200 group-hover:scale-110" />
              <span className="inline">{item.name}</span>
            </Link>
          ))}
          
          {isLoggedIn ? (
            <button
              onClick={handleLogout}
              className="flex items-center gap-2 px-4 py-2.5 text-sm font-medium text-gray-400 hover:text-white hover:bg-white/5 rounded-xl transition-all duration-200 ease-in-out ml-2"
            >
              <LogOut size={18} strokeWidth={2} className="transition-transform duration-200 group-hover:scale-110" />
              <span className="inline">Logout</span>
            </button>
          ) : (
            <button
              onClick={() => setShowLoginModal(true)}
              className="flex items-center gap-2 px-4 py-2.5 text-sm font-medium text-gray-400 hover:text-white hover:bg-white/5 rounded-xl transition-all duration-200 ease-in-out ml-2"
            >
              <LogIn size={18} strokeWidth={2} className="transition-transform duration-200 group-hover:scale-110" />
              <span className="inline">Login</span>
            </button>
          )}
        </div>
      </div>

      {showLoginModal && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50">
          <div className="bg-[#1a1b1e] rounded-2xl p-8 max-w-md w-full mx-4 shadow-2xl border border-gray-800">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-2xl font-bold text-white">Owner Login</h2>
              <button
                onClick={() => setShowLoginModal(false)}
                className="text-gray-400 hover:text-white transition-colors duration-200"
              >
                âœ•
              </button>
            </div>
            <OwnerLogin onLoginSuccess={handleLoginSuccess} />
          </div>
        </div>
      )}
    </>
  )
} 